@import 'theme/variables';

.TransactionEditor__tabs {
  padding-top: 15px;
  padding-left: 20px;
  padding-right: 20px;
}

.TransactionEditor__tab.slds-tabs__content {
  padding-bottom: 0;
}

.TransactionEditor {
  .DesignerAppLayout__view {
    margin: 0;
  }

  .TransactionEditor__standard {
    padding: 10px;
    padding-left: 25px;
    padding-bottom: 5px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
}

.SLAClockSection__message {
  display: flex;
  align-items: center;

  .SLAClockSection__text {
    padding-left: 20px;
  }
}

.SLAClockSection__message .SLAClockSection__icon {
  padding-left: 20px;

  .Icon {
    fill: $color-text-placeholder-inverse;
  }
}



