@import 'theme/variables';

.PropertiesSideBar__container {
  .Section.slds-is-open .Section__content {
    overflow: visible;
    padding-left: 10px;

    .Button--type-link {
      margin-left: -10px;

    &.FieldProperties__lookup-Text {
        margin-left: 5px;
      }
    }
  }
}

.FieldProperties__row-element {
  padding-bottom: 20px;
}

.FieldProperties__toggle-row {
  padding-bottom: 10px;
}

.FieldProperties__lookup-name {
  padding-top: 6px;
}

.Icon.FieldProperties__lookup-check--icon {
  fill: $color-link-linktext;
}

.Icon.FieldProperties__lookup-check--icon--disable {
  opacity: '0.5';
}

.Icon.FieldProperties__lookup-formfill-check--icon {
  fill: $color-link-linktext;
}

.FieldProperties__lookup-container .Text {
  padding-top: 5px;
  text-align: right;
}

.FieldProperties__lookup-context {
  padding-top: 15px;
  padding-left: 6px;
}



