@import 'theme/variables';

.PageButtons__btn--row {
  display: flex;
  justify-content: space-between;
  height: 24px; //empty title (text) on a list item does not respect the height.
}

.PageButtons__btn-properties,
.PageButtons__btn--row {
  background: transparent;
}

.PageButton.editing {
  background-color: $color-background-row-active;
}

.PageButtons__row-actions {
  display: none;
}

.PageButton:hover .PageButtons__row-actions,
.PageButton.editing .PageButtons__row-actions {
  display: block;
}

.List__item.PageButton:hover {
  .PageButton__title {
    width: 60%;
  }
}

.List__item.PageButton {
  max-width: 320px;
}

.PageButton__title {
  line-height: 24px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.PropertyItem__label {
  flex: 2;
}

.PageButton__property--level1 {
  padding-left: 15px;
}

.PageButton__property--web {
  padding-top: 15px;
}

.PageButton__property--ipad,
.PageButton__property--web {
  justify-content: space-between;
}

.PageButton__error {
  padding-left: 8px;
  outline: 0;
  position: relative;

  .Icon {
    fill: $color-background-toast-error;
  }
}

.Icon.PageButton__error--icon {
  fill: $color-white;
}



