@import 'theme/variables';

.List.FormulasTab__section-list {
  border: 0;
}

.List__item.FormulasTab__section-list-row {
  padding: 0;
  border-top: 0;
}

.Buttons__edit-button,
.Buttons__delete-button {
  margin-right: 5px;
}

.FormulasTabRow {
  .FormulasTabRow__action-buttons {
    visibility: hidden;
    padding: 5px;
    text-align: right;
  }

  .FormulasTabRow__item {
    padding: 5px;
  }
}

.FormulasTabRow:hover {
  background-color: $list-item-hover-color;

  .FormulasTabRow__action-buttons {
    visibility: visible;
  }
}

.List__item.FormulasTab__section-list-row:last-child {
  border-bottom: 1px solid $color-border;
}

.NoFormulaList {
  border-right: none;
  border-left: none;
}

.FormulasTab__section-buttons {
  padding-top: 10px;
  padding-bottom: 5px;
  padding-left: 2px;
}

.FormulaList__header {
  background-color: $brand-background-primary;
  padding: 5px;
  border-bottom: 1px solid $brand-light-active;
  border-top: 1px solid $brand-light-active;

  .Formula__name {
    padding-left: 10px;
  }
}



