@import 'theme/variables';

.FormulaEditorHeader {
  display: flex;
  align-items: center;

  .Picklistcontainer {
    display: flex;
    flex-flow: column;
  }
}

.FormulaEditorHeader__radiogroup {
  display: flex;
  flex-flow: column;
  max-height: 50px;
  z-index: 90002;
  padding-top: 5px;
  padding-left: 15px;
  flex: 1;
  margin-top: 5px;

  .slds-form-element__control {
    display: flex;
    flex-flow: row;
    justify-content: space-evenly;
  }
}

.FormulaExecutionLabel {
  padding-left: 18px;
}



