@import 'lib/package';

.Tree {
  .Tree__branch {
    outline: 0;

    .toggle-icon {
      margin-top: 0;
    }

  }

  .Tree__leaf {

    .item-icon {
      margin-left: 8px;
    }

    .slds-text-color--weak {
      color: $color-gray-9;
    }

    .leaf-data {
      display: flex;
      width: 100%;
      min-width: 0;
    }

    .leaf-action {
      display: flex;
      align-items: center;
      margin: 0 10px;

      .Button {
        align-self: auto;
        margin-top: initial;
        border: none;

        &:focus {
          box-shadow: none;
        }
      }
    }
  }

  em {
    font-style: normal;
    background-color: $color-background-highlight-search;
  }
}



