@import 'lib/package';

.LookupSearch__icon-wrapper {
  cursor: pointer;
  outline: none;
}

.Lookup {
  .Lookup__search {
    flex-wrap: nowrap;
    -ms-flex-wrap: nowrap;
    -webkit-flex-wrap: nowrap;
  
    .slds-lookup[data-scope="multi"] {
      .react-slds-lookup-scope-selector {
        min-width: 3rem;
  
        .slds-dropdown-trigger {
          margin-left: 0;
  
          .slds-button {
            padding: 0 0.25rem;
          }
        }
      }
      
      .slds-box--border {
        background-color: $color-background-secondary;
  
        .slds-input--bare {
          height: 2.15rem;
          width: 100%;
        }
      }

      .slds-box--border.react-slds-box-disabled {
        background-color: $color-background-input-disabled;
        border-color: $color-border-input-disabled;
        cursor: not-allowed;
      }

      .slds-input::placeholder {
        color: $color-text-inverse-weak;
        font-style: italic; 
      }
    }
  }

  .Lookup__menu {
    min-width: unset;
    max-width: unset;
    position: absolute;
    top: 30px;
  }

  .Lookup__menu.v-align{
    bottom: 100%;
  }

  .Lookup__menu.h-align{
    left: auto;
    right: 0;
  }

  .Icon {
    use {
      pointer-events: auto;
      cursor: pointer;
    }
  }
}



