@import 'theme/variables';

.FormulaEditorForm-list {
  %left-right-spacing {
    padding-left: 2rem;
    padding-right: 2rem;
  }

  .FormulaEditor__footer {
    text-align: right;
    border-top: 1px solid $border-default-color;
  }

  .List__header {
    border-bottom: 0;
    background: $color-background-secondary;
    padding-top: 10px;
    padding-left: 13px;
  }

  .ObjectLabel {
    padding-right: 30px;
  }
}

.List.FormulaEditorForm-list {
  border: 0;
  display: flex;
  flex-flow: column;
  height: 100%;
}

.Edtior__container {
  display: flex;
  flex-flow: row;
  height: calc(100vh - 200px);

  .Tree__container {
    min-width: 380px;
    width: 380px;
    height: calc(100vh - 200px);
    margin-right: 20px;
  }

  .Formula__container {
    width: 100%;
    height: calc(100vh - 200px);
    border: 1px solid $border-default-color;
  }

  .Tabs {
    height: 100%;
  }

  .Tab__content {
    height: calc(100% - 41px);
    padding-right: 5px;
    padding-left: 5px;
    padding-top: 5px;
  }

  .slds-tree-container {
    overflow-y: auto;
    height: 100%;
  }
}

.Formula__textarea--container {
  padding-left: 10px;
  flex: 2;
}



