@import 'theme/variables';

.CloneTransaction__body {
  padding: 26px 40px;

  .BadgeList {
    padding-top: 5px;
  }
}

.CloneTransaction__type[name=entitySubType] {
  border-bottom: 1px solid $border-default-color;
}

.CloneTransaction__taglabel {
  display: flex;
  margin-bottom: 5px;

  .CloneTransaction__withicon {
    margin-left: 5px;
  }
}

.CloneTransaction__withicon {
  &:hover .slds-icon {
    fill: $color-icon-on-hover;
    opacity: 1;
  }
}

.CloneTransaction__taglabel .Label__text,
.CloneTransaction__taglabel .CloneTransaction__withicon {
  display: inline-block;
}



