@import 'theme/variables';

/* stylelint-disable-next-line */
.DataGrid .rt-thead .rt-th.DataTableCell__header--align-center > div:first-child {
  padding-left: 15px;
}

.DataTableCell--no-overflow {
  text-overflow: none;
}

.DataTableCell--overflow {
  .ObjectRelation,
  .Popover,
  .Popover__trigger {
    display: inline;
  }

  .Popover {
    .Text {
      color: $color-link-linktext;
    }
  }
}

.DataGrid.DataGrid__list-view {
  .Icon.TypesCell__error {
    fill: $color-background-error-dark;
  }

  .Icon.TypesCell__warning {
    fill: $color-background-warning;
  }

  .Row__action-buttons {
    visibility: hidden;
  }
}

.DataGrid .rt-tr:hover {
  .Row__action-buttons {
    visibility: visible;
  }
}

.ActionMenu__column {
  display: flex;
  align-items: flex-end;
  text-align: right;
  justify-content: flex-end;
}

.DataGrid .rt-tbody {
  position: initial;
}

.DataGrid__cell {
  .NameCell__invalid-object {
    color: $color-background-error-dark;
  }

  .NameCell__title-color {
    color: $color-text-link;
  }

  .selectorCell .slds-form-element {
    margin-bottom: 0;
  }

  .LinkedSFM__name {
    margin-top: 5px;
  }
}

.NameCell__wrapper {
  overflow: hidden;
  text-overflow: ellipsis;
  display: flex;
  align-items: center;
  justify-content: center;
}

.NameCell {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}



