@import 'theme/index';

.MappingEditorRow {
  .ValueTypeColumn,
  .FieldValueColumn {
    padding-right: 15px;
  }
}

.MappingEditorList .MappingRow {
  padding-top: 5px;
  padding-bottom: 5px;

  .Grid.slds-grid {
    min-height: 36px;
  }
}

.List .List__item.MappingRowError {
  border: 1px solid $color-background-error-dark;
}

.MappingEditorList .EditModeMapping__list:hover {
  background-color: $list-item-hover-color;
  cursor: pointer;
}

.MappingEditorRow__gridrow {
  .ReadOnlyMappingRow__label:not(.RowFieldName__name) {
    padding-top: 8px;
  }

  .FormElement {
    padding-top: 3px;
  }

  .ReadOnlyMappingRow__additional-label,
  .ReadOnlyMappingRow__literal-label {
    padding-top: 8px;
  }

  .slds-form-element {
    margin-bottom: 0;
  }

  .MappingRow__refresh-icon,
  .AdditionalMappingIcon,
  .RemoveAdditionalMappingIcon,
  .MappingEditorRowClose,
  .NewWindowIconCol {
    margin-top: 7px;
  }

  .TargetObjectFieldColumn {
    width: $col-medium-width; //since Label does not support size
    @include text-ellipsis;

    padding-right: 30px;
    display: block;
  }

  .ValueOrFunctionLabelColumn {
    margin-right: 15px;
    width: $col-small-width;
  }
}



