@import '@svmx/ui-components-lightning/lib/package';
// fonts
$font-family: sans-serif;

// colors
$color-white: $brand-header;
$color-grey: $color-gray-11;
$color-icon-on-hover: $brand-header-contrast-cool-active;
$color-light-grey: $color-gray-7;
$color-link-linktext: $brand-accessible;
$border-default-color: $color-gray-5;
$designer-button-max-width: 300px;
$designer-titletext-color: #16325c;
$mapping-modal-type-border-color: #e0e5ee; //Color mapping will be replaced by slds color once confirmed by Eunah
$error-state-fill-color: $color-border-destructive-hover;
$list-item-hover-color: #ecebea;

//column width
$col-medium-width: 20rem;
$col-small-width: 15rem;

$zindexlayers: (
  'modal':10000
);

@function zindexprovider($layer) {
  // ... where the magic happens.
  @if not map-has-key($zindexlayers, $layer) {
    @warn 'No layer found for `#{$layer}` in $zindexlayers map. Property omitted.';
  }
  @return map-get($zindexlayers, $layer);
}



