@import 'lib/package';

.DataGrid {
  min-height: 200px;
  max-height: 100vh;
  background-color: $color-gray-1;
  border-right: none;
  border-left: none;

  &.-no-row-border .rt-tbody .rt-tr-group {
    border-bottom: none;
  }

  &.-col-border {
    .rt-thead .rt-th,
    .rt-tbody .rt-td {
      &:not(:last-child) {
        border-right: 1px solid $brand-light-active;
      }
    }
  }

  &.-col-sortable .rt-thead .rt-th {
    height: 35px;

    /**
    * inner element that gets arrow icon
    * non-resizable column config needs selector div:first-child
    */
    .rt-resizable-header-content,
    div:first-child {
      margin-right: 4px;
      display: inline-block;
      padding: 0 15px 0 0;
    }

    &.DataGrid__header--align-center > div:first-child {
      padding-left: 15px;
    }

    &.-cursor-pointer {
      &:not(.-sort-asc):not(.-sort-desc):hover {
        // header onmousehover + non-resizable column to display sort icon
        .rt-resizable-header-content,
        div[class='']:first-child {
          background: no-repeat 100% center
            url(data:image/svg+xml;base64,);
        }

        // resize handler
        .rt-resizer {
          background-color: $brand-light-active;
        }
      }
    }

    &.-sort-asc {
      // header onclick ascending + non-resizable column to display sort icon
      .rt-resizable-header-content,
      div[class='']:first-child {
        background: no-repeat 100% center
          url(data:image/svg+xml;base64,);
      }

      &:focus {
        border: 1px solid $color-text-link-hover;

        .rt-resizer {
          background-color: $color-text-link-hover;
        }
      }
    }

    &.-sort-desc {
      // header onclick descending + non-resizable column to display sort icon
      .rt-resizable-header-content,
      div[class='']:first-child {
        background: no-repeat 100% center
          url(data:image/svg+xml;base64,);
      }

      &:focus {
        border: 1px solid $color-text-link-hover;

        .rt-resizer {
          background-color: $color-text-link-hover;
        }
      }
    }
  }

  &.DataGrid__list-view {
    .Icon:not(.slds-button__icon) {
      fill: $color-gray-6;
      background-color: transparent;
    }

    .Icon:hover {
      cursor: pointer;
      fill: $color-gray-8;
      background-color: transparent;
    }
  }

  &.DataGrid--fixed-header {
    .rt-table {
      overflow-y: hidden;
    }

    .rt-thead {
      overflow-x: hidden;
      overflow-y: scroll;
    }

    .rt-tbody {
      overflow-x: hidden;
      overflow-y: scroll;
    }
  }
  
  .rt-tr-group {
    flex: 0 0 auto;
  }

  .rt-resizer {
    right: 0;
    width: 4px;
  }

  .rt-thead.-header,
  .rt-tbody .rt-tr-group {
    border-bottom: 1px solid $brand-light-active;
    box-shadow: none;
  }

  .rt-thead {
    .rt-th.-sort-asc,
    .rt-td.-sort-asc,
    .rt-th.-sort-desc,
    .rt-td.-sort-desc {
      box-shadow: none;
    }

    .rt-tr {
      background-color: $color-gray-2;
    }

    .rt-th {
      display: flex;
      padding: 0.75rem 5px;
      text-transform: uppercase;
      text-align: left;
    }
  }

  .rt-tbody {
    position: relative;
    overflow: initial;

    .rt-td {
      display: flex;
      padding: 5px;
      min-height: 30px;

      .Popover {
        .Popover__trigger {
          white-space: nowrap;
          text-overflow: ellipsis;
          overflow: hidden;
        }
      }

      .Icon {
        margin: 1px 0 2px;
      }
    }
  }

  .rt-th {
    outline: none;
    letter-spacing: 0.0625rem;
  }

  .rt-tr {
    align-items: center;
  }

  .rt-thead .rt-th,
  .rt-tbody .rt-td {
    border-right: none;
    padding: 9px 5px;

    &:first-child {
      padding-left: 25px;
    }

    &:last-child {
      padding-right: 25px;
    }
  }

  .DataGrid__cell--align-center,
  .DataGrid__header--align-center {
    justify-content: center;
  }

  .DataGrid__cell--align-right,
  .DataGrid__header--align-right {
    flex-direction: column;
    align-items: flex-end;
  }

  .pagination-bottom {
    background-color: $color-gray-2;
    .-pagination {
      width: 350px;
      position: relative;
      margin: 0 auto;
      box-shadow: none;
      border-top: none;
      justify-content: normal;
        .-center {
          .-pageInfo .-pageJump input {
            width: 45px;
            height: 25px;
            margin-right: 3px;
          }             
          .-pageSizeOptions {
            margin: 0;
            position: absolute;
            right:0;    
            margin-right: 30px;      
            &:before {
              content: "Rows";
              padding-right: 10px;
              font-weight: bold;
            }
            select {
              width: 60px;
              height:30px;
            }
          }
        }
        .-previous {
          margin-right: 10px;
          .-btn:before {
            width: 0;
            height: 0;
            border-right: solid 6px $salesforce-blue;
            border-bottom: solid 6px transparent;
            border-top: solid 6px transparent;
            content: "";
            display: block;
            margin: 0 auto;
          }
        }
        .-next {
          margin-left: 10px;
          .-btn:before { 
            content: "";
            width: 0;
            height: 0;
            border-left: solid 6px $salesforce-blue;
            border-bottom: solid 6px transparent;
            border-top: solid 6px transparent;
            display: block;
            margin: 0 auto;
          }
        }       
        
        .-previous, .-next, .-center {
          flex: none;
        }
        .-previous .-btn,
        .-next .-btn {
            height: 32px;
            width: 30px;
            border: 1px solid $color-gray-5;
            border-radius: 4px;
            background-color: white;
        }
            
    }
  }
}



