@import 'lib/package';

.DateInput {
  position: relative;

  .Input::-ms-clear {
    display: none;
  }

  .Datepicker {
    position: absolute;
    z-index: $z-index-dropdown;
    left: 0;
    top: 32px;
  }

  .DateInput__calendar-icon {
    z-index: $z-index-calender-icon;
  }
}

.Datepicker {
  width: 334px;
  outline: none;
  border: 1px solid $color-border;
  border-radius: .25rem;
  background: #fff;
  box-shadow: $card-shadow;

  .Datepicker__day {
    outline: none;
  }

  .Datepicker__extension {
    padding: 8px;
    text-align: center;
  }
}

.slds-has-error .Datepicker .Select {
  border: 1px solid #d8dde6;
  box-shadow: none;
}



