@import 'lib/package';

.Icon__svmx {
  &.Icon--size-x-small svg {
    height: 16px;
    width: 16px;
  }
  
  &.Icon--size-small svg {
    height: 24px;
    width: 24px;
  }
  
  &.Icon--size-medium svg {
    height: 32px;
    width: 32px;
  }
  
  &.Icon--size-large svg {
    height: 48px;
    width: 48px;
  }

  &.Icon--theme-info .svg-foreground {
    fill: $color-gray-9;
  }

  &.Icon--theme-success .svg-foreground {
    fill: $color-border-success-dark;
  }

  &.Icon--theme-warning .svg-foreground {
    fill: $color-border-warning;
  }

  &.Icon--theme-error .svg-foreground {
    fill: $color-border-error;
  }
}



