@import 'theme/variables';

.LayoutEditor__section--related {
  width: 100%;
  overflow: hidden;

  .Section__title {
    cursor: move;
  }

  .Section.Section__bordered .Section__content {
    width: 100%;
    display: inline-grid;
    overflow: hidden;
    padding-bottom: 0;
  }

  .EmptyLayoutSectionMessage {
    padding-bottom: 12px;
  }

  .PageLayout__field {
    margin: 0;
    padding-right: 20px;
  }

  .Section.Section__bordered {
    margin-top: 0;
    margin-bottom: 0;
  }
}

.LayoutEditor__section--related.LayoutEditor__section--related-error {
  padding-top: 20px;
}

.LayoutEditor__section--related-scroller {
  display: flex;
  min-width: 100%;
  overflow-x: auto;
  padding: 20px;
}

.LayoutEditor__section--reorder .LayoutEditor__section--related-scroller {
  display: grid;
  row-gap: 20px;
  justify-content: center;
}

.LayoutEditor__section--reorder {
  .PageLayout__field {
    padding-right: 0;
  }

  .LayoutField:hover {
    cursor: move;
  }

  .LayoutField__empty {
    border-style: dashed;
    border-color: $color-border-primary;
    padding-right: 20px;
  }

  .DraggableLayoutField__dragging {
    margin-right: 0;
  }
}

/* LayoutField__isRemovable is a component level css class */
/* stylelint-disable selector-class-pattern */
.DraggableLayoutField.DraggableLayoutField__dragging.LayoutField__isRemovable .LayoutField {
  padding: 0;
  cursor: move;
}

.DraggableLayoutField.DraggableLayoutField__dragging.LayoutField__isRemovable .LayoutField:hover .LayoutField__close {
  display: none;
  cursor: move;
}
/* stylelint-enable */
.DraggableLayoutField__dragging {
  margin-right: 20px;
}

.DraggableLayoutField__empty {
  height: 35px;
  padding: 2px;
}

.DraggableLayoutField__preview {
  cursor: move;
  display: none;
  opacity: 0.9;
}

.DraggableLayoutField__preview.dragging {
  display: block;
  margin-top: -28px;
  cursor: move;
  opacity: 0.9;
}

.DraggableLayoutField .LayoutField:hover {
  cursor: move;
}

.RelatedSection__drop--target {
  &.dragging {
    opacity: 0.9;
    border: 2px $color-border-primary dashed;
  }

  width: 100%;
}



