@import 'theme/variables';

.LookupValuePicker {
  display: flex;

  .RecordLoading__spinner {
    display: flex;
    position: inherit;
    margin-top: 14px;
  }

  .LookupValuePicker__open {
    margin-left: 5px;
  }
}

.LookupValuePicker__open--error {
  fill: $error-state-fill-color;
}



