@import 'theme/variables';

.SharedLayoutPicker {
  display: flex;
  flex-flow: column;

  .SharedLayoutPicker__common--actions {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    min-height: 38px;
  }

  .SharedLayoutPicker__standard--layout {
    font-size: 11px;
    flex: 2;
    text-align: left;
    display: flex;
    align-items: center;
    padding-right: 10px;
    padding-bottom: 5px;
  }

  .SharedLayoutPicker__form {
    display: flex;
    flex-flow: row;
    align-items: flex-start;
    justify-content: center;
    border: 1px solid $color-border-primary;
    padding-left: 10px;
    padding-top: 5px;
    padding-right: 10px;
  }

  .SharedLayoutPicker__form .SharedLayoutPicker__entity--actions {
    width: 80px;
    height: 78px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .SharedLayoutPicker__form .SharedLayoutPicker__name--input {
    width: 25rem;
    padding-right: 15px;
  }

  .SharedLayoutPicker__form .SharedLayoutPicker__description-input {
    flex: 1.5;
  }

  .SharedLayoutPicker__form textarea {
    height: 32px;
    min-height: 32px;
    padding-left: 10px;
    padding-bottom: 0;
    padding-top: 0;
    line-height: 32px;
  }
}

.SelectLayoutModal__content {
  height: 65vh;

  .SelectLayoutModal__list {
    height: 65vh;
  }
}

.CloneLayoutModal__container {
  padding: 20px;
}



