@import 'theme/variables';

.List.DVRTab__section-list {
  border: 0;
}

.List__item.DVRTab__section-list-row {
  padding: 0;
  border-top: 0;
}

.List__item.DVRTab__section-no-rules {
  padding: 0;
}

.Text.DVRTab__no-rules {
  text-align: left;
  color: $color-text-tertiary;
  padding-left: 17px;
}

.Buttons__edit-button,
.Buttons__delete-button {
  margin-right: 5px;
}

.DVRTabRow {
  .DVRTabRow__action-buttons {
    visibility: hidden;
    padding: 5px;
  }

  .DVRTabRow__item {
    padding: 5px;
  }
}

.DVRTabRow:hover {
  background-color: $color-background-row-hover;

  .DVRTabRow__action-buttons {
    visibility: visible;
  }
}

.List__item.DVRTab__section-list-row:last-child {
  border-bottom: 1px solid $color-border;
}

.DVRTab__section-buttons {
  padding-top: 10px;
  padding-bottom: 5px;
  padding-left: 2px;
}

.DVRMessageModal__container {
  padding: 10px 5px 5px;

  .DVRMessageModal__textarea,
  .DVRMessageModal__textarea :hover,
  .DVRMessageModal__textarea :focus,
  .DVRMessageModal__textarea :active {
    resize: none;
  }
}

.Icon.DvrTab__error-icon {
  fill: $color-background-error-dark;
}

.Icon.DvrTab__warning-icon {
  fill: $color-background-warning;
}



