@import 'theme/variables';

.MappingTypeModal {
  .slds-modal__container {
    max-width: 70%;
    width: 70%;
  }
}

.MappingTypeModal__container {
  padding: 30px;
  cursor: default;
  max-height: 60%;
}

.RadioWrapper--active {
  .Text {
    color: $color-link-linktext;
  }
}

.MappingTypeModal__description {
  padding-left: 18px;
  font-size: 12px;
  line-height: 15px;
}

.MappingTypeModal__descriptiontext {
  margin-top: 4px;
  margin-bottom: 20px;
}

.MappingTypeModal__iconcontainer {
  display: flex;
  align-items: center;
}

.MappingTypeModal__objectlistcontainer {
  display: flex;
  flex-flow: row;
  padding-top: 17px;
  padding-bottom: 24px;
}

.MappingTypeModal__sourceobject {
  // This was done to remove the outline as the div contains tab index (SFMDZ-713)
  .PicklistFactory {
    outline: 0;
  }

  padding-right: 16px;
}

.MappingTypeModal__targetobject {
  // This was done to remove the outline as the div contains tab index (SFMDZ-713)
  .PicklistFactory {
    outline: 0;
  }

  padding-left: 16px;
}



