@import 'theme/variables';

.RuleEditorContainer {
  padding-top: 10px;
}

.FieldValueType--read-only svg {
  display: none;
}

.FieldValueType--read-only .slds-input {
  background-color: $brand-light;
}

.RuleEditor__footer {
  text-align: right;
}

.RuleEditorList {
  .RuleEditor__list-item--no-rows {
    display: inline-block;
    height: 340px;
    width: 100%;
  }

  .AddNewRowListItem {
    outline: 0;
  }
}

.RuleEditorRowStack__add-row {
  cursor: pointer;
  margin-left: 15px;
}

.RuleEditorRow {
  &:hover {
    background-color: $brand-light;
  }

  &:hover .RemoveItemColumn {
    display: flex;
  }

  .Grid__item {
    padding-right: 15px;
  }

  .SequenceNumberColumn {
    display: flex;
    min-width: 50px;
  }

  .SequenceNumber {
    display: flex;
    padding-top: 5px;
    justify-content: flex-end;
    width: 100%;
    align-content: flex-end;
    padding-right: 10px;
  }

  .RemoveItemColumn {
    padding-top: 5px;
    vertical-align: middle;
    display: none;
  }

  .EmptyGridItem {
    display: none;
  }
}

.RuleEditorList .List__header {
  padding: 10px 25px;
  border-bottom: 0;
}

.RuleEditorList .RuleEditor__body {
  padding: 10px 25px;
}



