.LookupEditorContainer__form {
  .Button--type-link {
    padding-right: 45px;
    text-overflow: unset;
  }

  .LookupEditorList {
    border: 0;

    .LookupEditorList__item {
      border-top: 0;
      padding-top: 0;
      padding-bottom: 0;
    }

    .LookupEditorCheckbox__item {
      padding-top: 10px;
      padding-bottom: 10px;
    }
  }

  .DuelingPicklist__content {
    .DuelingPicklist__source-menu {
      width: auto;
      max-width: 100%;
    }

    .DuelingPicklist__target-menu {
      width: auto;
      max-width: 100%;
    }

    .Menu__list {
      overflow-y: unset;
    }
  }

  .CollapsibleSection__content {
    padding: 20px;
    padding-bottom: 5px;
    overflow: unset;

    .NoRowsFound {
      margin: 5px;
      text-align: center;
    }
  }

  .RuleEditorRow .Grid__item {
    padding-right: 5px;
  }
}



