@import 'theme/variables';

.ListFilter {
  padding: 8px 0 16px;

  .ListFilter__text {
    color: $color-grey;
    font-weight: 400;
  }
}

.ListFilter__container {
  padding-left: 2px;
  display: flex;
  flex-direction: row;
  padding-top: 8px;

  .ListFilter__new-action-button {
    margin-left: auto;
  }

  .ListFilter__dropdown--wrapper {
    padding-left: 20px;
  }

  .ListFilter__dropdown button {
    color: $color-grey;
  }
}

.ListFilter__dropdown > button > .Icon {
  color: $color-light-grey;
}



