@import 'theme/variables';

.MappingEditorList {
  %left-right-spacing {
    padding-left: 2rem;
    padding-right: 2rem;
  }

  .MappingEditorForm__header-col {
    display: flex;
    align-items: center;
  }

  .MappingEditorForm__object-name-label {
    display: flex;
    color: $designer-titletext-color;
  }

  .MappingEditor__footer {
    text-align: right;
  }

  .MappingEditorForm__toggle-btn {
    margin-right: 50px;
  }
}



