@import 'theme/variables';

.ReadOnlyMappingRow {
  user-select: none;

  .ErrorAndEditIconContainer {
    text-align: right;
    margin-top: 5px;
  }

  .Icon.EditIcon {
    visibility: hidden;
  }

  .Icon.ErrorIcon {
    fill: $color-background-error-dark;
    margin-right: 10px;
  }
}

.ReadOnlyMappingRow:hover {
  .Icon.EditIcon {
    visibility: visible;
  }
}



