@import 'lib/package';

.ColorPicker { 
  position: relative;
  .ColorPicker__popover {
    position: absolute;
    top:40px;
    z-index: $z-index-rc-trigger-popup;
  }  
  .ColorPicker__cover {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }
  .ColorPicker__selected-color {
    border: 1px solid $color-border;
    border-radius: .25rem;
    padding: 0 1rem 0 .75rem;
    line-height: 1.875rem;
    width: 32px;
    height: 32px;
  }
}



