.Tabs.TransactionEditor__tabs {
  min-height: calc(100vh - 70px);
  display: flex;
  flex-flow: column;

  .TransactionEditor__tab.OutputDocumentDesign {

    flex: 2;
    display: flex;
    padding-top: 15px;
  }

  .OutputDocEditorForm {
    flex: 1;
    display: flex;
    flex-flow: column;
  }

  .OutputDocEditor {
    display: flex;
    flex: 2;
    flex-flow: column;
  }

  .OutputDocEditor__container {
    flex: 1;
    display: flex;
    flex-flow: row;
  }

  .HtmlTemplateEditor.OutputDocEditor__template--editor {
    flex: 3;
    flex-grow: 1;
    flex-shrink: 0;
    height: 100%;
  }

  .cke {
    border: 0;
    height: calc(100% - 25px);
  }

  .Inline__spinner {
    display: inline-block;
    height: 100px;
    position: relative;
  }
}

.HtmlTemplateEditor__fields--tree,
.HtmlTemplateEditor__function--tree {
  .Tree__branch .Button {
    outline: 0;
    box-shadow: none;
  }
}

.HtmlTemplateEditor__toolbox {
  min-width: 380px;
  display: flex;
  flex-flow: column;
  padding-top: 5px;

  .Tabs {
    height: 100%;
  }

  .Inline__spinner--msg {
    padding-top: 25px;
  }

  .HtmlTemplate__editor--loader {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: column;
  }

  .HtmlTemplate__editor--loader,
  .Tab__content {
    height: calc(100% - 45px);
    padding: 10px 3px 0 0;
    max-width: 380px;
  }

  .slds-tree {
    overflow: auto;
    height: calc(100vh - 370px);
  }
}



