@import 'theme/variables';

.SidebarLayout.LayoutEditor__sections {
  display: flex;
  padding-top: 20px;
}

.LayoutEditor__region--content {
  flex: 3;
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  padding-top: 5px;
  padding-left: 5px;
  background-color: $color-background-backdrop-tint;
  margin-right: 18px;
}

.SidebarLayout.RegionSidebar.LayoutEditor__right--sidebar {
  width: 360px;
  min-width: 360px;
}

.LayoutEditor__related--sections-wrapper {
  border-top: 1px solid $color-contrast-primary;
  margin-top: 50px;
}

.LayoutEditor__header--sections-wrapper,
.LayoutEditor__related--sections-wrapper {
  display: flex;
  flex-flow: column;
  align-items: flex-start;
  width: 100%;
  padding: 10px;
}

.LayoutEditor__section--columns {
  display: flex;
  justify-content: center;
  padding-left: 3px;
  padding-right: 3px;
}

.LayoutEditor__section--column {
  flex: 1;
  flex-grow: 0;
  padding-right: 15px;
  padding-left: 15px;
}

.LayoutEditor__properties--sidebar-wrapper {
  padding-right: 3px;
}

.Section__addon .Button--type-link {
  font-size: 13px;
}

.LayoutEditor__section {
  width: 100%;

  .Section__title {
    cursor: move;
  }

  .slds-section {
    padding-top: 1rem;
    padding-bottom: 1rem;
  }
}

.PageLayout__field--header--empty {
  margin-bottom: 20px;
}

.LayoutField.LayoutField__empty {
  height: 32px;
}

.LayoutField.LayoutField__empty.LayoutField__placeholder {
  border: 1px $color-border-primary dashed;
  border-radius: 3px;
}

.LayoutField__placeholder {
  margin-bottom: 20px;
}

.HeaderSection__drop--target {
  width: 100%;
  padding-bottom: 1rem;
}

.LayoutEditor__section--header .Section.slds-is-open .Section__content {
  overflow: hidden;
}

.LayoutEditor__section--header.dropallowed.hovering.Section--highlighted .Section__content {
  border: 0;
}

.HeaderSection__drop--target.dropallowed.hovering {
  .LayoutEditor__section {
    opacity: 0.1;
  }

  border: 2px $color-border-primary dashed;
}

.LayoutEditor__layout--heading {
  width: 100%;
  display: flex;
  justify-content: space-between;

  .PageButton__page--settings {
    position: relative;
    top: -26px;
    left: 14px;

    /* stylelint-disable max-nesting-depth */
    .PageSettings__icon.error {
      fill: $color-background-toast-error;
    }
  }
}

.PageLayout__field.Placeholder__field:hover .LayoutField__box,
.PageLayout__field.Placeholder__field:focus .LayoutField__box,
.PageLayout__field.Placeholder__field:active .LayoutField__box {
  border-color: $color-border-primary;
  outline: 0;
  background: transparent;
}

.HeaderSection__column {
  padding: 15px 15px 0;
  flex-grow: 0;
  overflow: hidden;
  width: 100%;

  .EmptyLayoutSectionMessage {
    margin-top: -15px;
  }
}

.LayoutEditor__section--columns.two-col-layout,
.LayoutEditor__section--columns.one-col-layout {
  .HeaderSection__column.HeaderSection__column--dragover {
    overflow: hidden;
  }
}

.LayoutEditor__section--columns.two-col-layout {
  .HeaderSection__column {
    flex-grow: 1;
    flex-basis: 0;
  }

  .HeaderSection__column.HeaderSection__column--dragover {
    overflow: hidden;
  }
}

.LayoutFieldPicker.LayoutFieldPicker__two-column .ModalContent {
  min-height: 80%;
  overflow: hidden;
  display: flex;
  flex-flow: column;

  .Menu.slds-dropdown {
    flex: 2;
  }

  .Menu__list {
    max-height: 92%;
  }
}

.LayoutFieldPicker.LayoutFieldPicker__two-column {
  .LayoutFieldPicker__add--fields {
    display: flex;
    flex-flow: column;
    max-height: 50px;
    min-height: 50px;
    background: $color-white;
    z-index: 90002;
    padding-top: 5px;
    padding-left: 15px;
    flex: 1;

    .slds-form-element__control {
      display: flex;
      flex-flow: row;
      justify-content: space-evenly;
    }
  }
}

.Draggable__section--header,
.Draggable__section--related,
.Draggable__field,
.Draggable__field--related,
.Draggable__section--header:focus,
.Draggable__section--related:focus,
.Draggable__field:focus,
.Draggable__field--related:focus {
  outline: 0;
}

.Draggable__section--header,
.Draggable__section--related {
  margin-top: 20px;
}

.Draggable__section--dragging .Section.Section__bordered {
  margin-top: 0;
  margin-bottom: 0;
}

.Draggable__section--dragging {
  .LayoutEditor__section--container,
  .LayoutEditor__section--related {
    border: 2px $color-border-primary dashed;
  }
}

.LayoutEditor__section--columns .HeaderSection__column:hover {
  background: $color-background-backdrop-tint;
}

.AddSpace__menu--icon {
  height: 10px;
  width: 10px;
}



