@import 'theme/variables';

// Import application theme definitions here
body {
  margin: 0;
  padding: 0;
  background-color: $color-white;
}

@mixin text-ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/** SFMDZ-346 Override the button from the ui-components to have ellipsis and fix max width */
.Button {
  max-width: $designer-button-max-width;
  @include text-ellipsis;
}

#portal-root {
  position: relative;
  z-index: 90500;
}

.DesignerAppLayout {
  .cursor-pointer {
    cursor: pointer;
  }

  .Button--type-link {
    border: 0;
  }

  .Button--type-link,
  .Button--type-link:hover,
  .Button--type-link:active,
  .Button--type-link:focus {
    background-color: transparent;
    padding-left: 0;
  }

  .Button--type-link:focus {
    box-shadow: none;
  }

  .required-indicator label::before {
    content: '* ';
    color: $color-background-error-dark;
    display: inline;
  }

  .Spinner__position {
    position: fixed;
  }
}



