@import 'theme/variables';

.StartPage {
  display: flex;
  justify-content: center;
}

.StartPage__list {
  list-style-type: none;
  padding-top: 120px;

  li a {
    display: block;
  }

  a:link,
  a:visited {
    color: $brand-contrast;
    text-decoration: none;
    display: block;
    padding: 30px 0;
    font-size: 18px;
  }

  .Icon {
    display: inline-block;
    border-radius: 60px;
    text-align: left;
    box-shadow: 0 0 2px #888;
    padding: 0.5em 0.6em;
    margin-right: 20px;
  }
}



