@import 'theme/variables';

.Field__direction {
  padding-left: 8px;
}

.SortingSection__fields {
  border: 0;
}

.List__item.SortingSection__row:hover {
  background-color: $list-item-hover-color;
}

.Text.SortingSection__no-rules {
  text-align: left;
  color: $color-text-tertiary;
  padding-left: 0;
}

.List__item.PageButton.SortingSection__row:hover {
  .SortField__wrapper {
    overflow: hidden;
    display: flex;
    width: 70%;

    .PageButton__title {
      width: 75%;
    }
  }
}




