@import 'theme/variables';

.HeaderCard-container {
  justify-content: center;
  display: flex;
  border-bottom: 1px solid $card-color-border;
  padding-bottom: 15px;
}

.ChildCard-container {
  padding-top: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;

  .Card {
    margin-bottom: 20px;
  }
}

.ChildCard-title {
  padding-top: 7px;
  padding-bottom: 7px;
}

.TransactionEditor__tabs .ObjectMapping {
  .slds-text-heading--medium {
    font-weight: bold;
  }
}

.ObjectCard.Card--type-mobile-view,
.ObjectCard.Card--type-standalone-create {
  height: 300px;

  .Card__content {
    height: 180px;
  }
}

.ObjectCard.Card--type-standalone-edit {
  height: 336px;

  .Card__content {
    height: 180px;
  }
}

.ChildCard-container {
  .ObjectCard.Card--type-standalone-edit {
    height: 300px;
  }
}

.ChildCard-container {
  .ObjectCard.Card--type-standalone-create {
    height: 260px;
  }
}



