@import 'theme/variables';

.GeneralSection__property--level1 {
  padding-left: 15px;
  padding-bottom: 15px;

  .SectionProperties__default--label {
    width: 170px;
  }

  .Button--type-link {
    line-height: 1.5;
  }

  .Icon.SectionProperties__mapping-set--icon {
    fill: $color-link-linktext;
    margin-right: 10px;
  }
}

.SectionProperties__default-values-text {
  padding-left: 0;
}

.SectionProperties__columns {
  padding-bottom: 8px;
  padding-top: 8px;
}

.SectionProperties__columns--options {
  .slds-form-element__control {
    padding-top: 10px;
    display: flex;
  }
}

.SectionProperties__columns--option {
  padding-left: 15px;
}

.List.SectionProperties__linkedsfm {
  border: 0;

  .List__item.SectionProperties__linkedsfm-row {
    padding: 10px;
  }

  .SectionProperties__linkedsfm-row-text {
    max-width: 243px;
    margin-right: 5px;
    margin-top: 3px;
  }
}

.SectionProperties__linkedsfm-row-actions {
  visibility: hidden;
}

.SectionProperties__linkedsfm-row:hover {
  background-color: $color-background-row-hover;

  .SectionProperties__linkedsfm-row-actions {
    visibility: visible;
  }
}



