@import 'lib/package';

.Section {
  .Section__title {
    position: relative;

    .Section__title--error {
      position: absolute;
      top: -20px;
      right: 0;
      z-index: $z-index-errors;

      .Icon {
        color: $color-text-error;
      }
    }

    .slds-button:focus {
      box-shadow: none;
    }

    .Section__title--action {
      justify-content: space-between;
      overflow: hidden;

      &>span {
        display: flex;
        align-items: center;
        outline: 0;

        .slds-section__title-action-icon {
          flex: 0 0 14px;
        }

        .Section__title--text {
          white-space: nowrap;
        }

        .Section__subtitle {
          font-size: 14px;
          font-weight: 200;
          color: $color-gray-8;
        }
      }
    }

    .Section__title--right-addon {
      display: flex;
      justify-content: flex-end;
      flex: 1 0;
      text-align: right;
    }
  }

  &.slds-is-open .Section__content {
    overflow: visible;
  }

  $section-border: 1px $color-gray-4 solid;

  &.Section__bordered {
    background-color: white;
    border: $section-border;

    .Section__title {
      border-radius: 0;
      border-bottom: $section-border;

      .slds-button.slds-section__title-action {
        background-color: inherit;
        cursor: default;
      }
    }

    .Section__content {
      padding-bottom: 12px;
    }
  }

  &.Section--highlighted {
    border: 2px solid $color-brand;
  }

  &.Section--error {
    border: 2px solid $color-border-error;
  }
}



