@import 'theme/variables';

.LayoutFieldPicker .ModalContent {
  min-height: 80%;
  overflow: hidden;

  .Menu__list {
    max-height: calc(100vh - 270px);
    height: calc(100vh - 300px);
    // this needs fix from ui-components. overriding for now to fix the search bar on tab in a menu item list .
    // to fit the height of modal
  }

  .Menu.slds-dropdown {
    border: 0;
    min-width: 100%;
  }
}

.LayoutFieldPicker .ModalFooter.slds-modal__footer {
  border: 0;
  box-shadow: none;
}

.LayoutFieldPicker__field-list .Menu__item--is-disabled > .Icon.Icon--selected {
  fill: $color-background-input-disabled;
}

.LayoutFieldPicker__menu--item .Menu__item-content {
  display: flex;
  width: 100%;
}

.LayoutField__item {
  width: 100%;
}

.LayoutField__datatype--icon {
  min-height: 16px;
  min-width: 16px;
}



