@import 'lib/package';

.Typeahead {
  .Typeahead__input {
    flex-wrap: nowrap;
    -ms-flex-wrap: nowrap;
    -webkit-flex-wrap: nowrap;
  
    .slds-lookup[data-scope="multi"] {
      .react-slds-lookup-scope-selector {
        min-width: 3rem;
  
        .slds-dropdown-trigger {
          margin-left: 0;
  
          .slds-button {
            padding: 0 0.25rem;
          }
        }
      }

      .slds-box--border {
        background-color: $color-background-secondary;
  
        .slds-input--bare {
          height: 2.15rem;
          width: 100%;
        }
      }
      
      .slds-box--border.react-slds-box-disabled {
        background-color: $color-background-input-disabled;
        border-color: $color-border-input-disabled;
        cursor: not-allowed;
      }
    }

    .slds-input::placeholder {
      color: $color-text-inverse-weak;
      font-style: italic; 
    }
  }

  .Icon {
    use {
      pointer-events: auto;
      cursor: pointer;
    }
  }

  .Typeahead__icon-wrapper {
    cursor: pointer;
    outline: none;
  }

  .Typeahead__menu {
    min-width: unset;
    max-width: unset;
    position: absolute;
    top: 31px;

    &.v-align {
      bottom: 100%;
    }
  
    &.h-align {
      left: auto;
      right: 0;
    }

    // add one row to the max-height of the menu
    .Menu__list {
      max-height: 340px;
    }

    .Menu__footer {
      background-color: $color-background;
      text-align: center;
      line-height: 1.25;
      
      .slds-text-heading--label {
        text-transform: none;
        font-weight: bold;
      }
    }
  }
}

.TagTypeahead {
  .TagTypeahead__list {
    margin-top: 5px;
  }
}



