@import 'theme/variables';

.SourceObjectUpdateTab__collapsible-section-error .Section__title.slds-section__title {
  border: 1px solid $color-background-error-dark;
}

.List.SourceObjectUpdateTab__section-list {
  border: 0;
}

.List__item.SourceObjectUpdateRow__read-only,
.List__item.SourceObjectUpdateRow__edit-row {
  .ActionTypeField__read-only {
    align-self: center;
  }

  .SourceObjectUpdateRow__action-buttons {
    align-self: center;
    display: inline-block;
    visibility: hidden;
    text-align: right;
  }
}

.List__item.SourceObjectUpdateRow__read-only:last-child,
.List__item.SourceObjectUpdateRow__edit-row:last-child {
  border-bottom: 1px solid $color-border;
}

.List__item.SourceObjectUpdateRow__read-only-error {
  border: 1px solid $color-background-error-dark;
}

.List__item.SourceObjectUpdateRow__read-only-error:last-child {
  border-bottom: 1px solid $color-background-error-dark;
}

.List__item.SourceObjectUpdateRow__read-only-error:not(:first-child) {
  border: 1px solid $color-background-error-dark;
}

.SourceObjectUpdateTab__section-list {
  .SourceObjectUpdateRow__read-only-error + .SourceObjectUpdateRow__read-only-error {
    border-top: 0;
  }
}

.List__item.SourceObjectUpdateRow__read-only:hover,
.List__item.SourceObjectUpdateRow__edit-row:hover {
  background-color: $list-item-hover-color;

  .SourceObjectUpdateRow__action-buttons {
    visibility: visible;
  }
}

.List__item.SourceObjectUpdateTab__section-no-rules {
  padding: 0;
}

.Text.SourceObjectUpdateTab__no-rules {
  text-align: left;
  color: $color-text-tertiary;
  padding-left: 17px;
}

.SourceObjectUpdateRow__subtitle {
  padding-bottom: 5px;
}

.List__item.SourceObjectUpdateRow__section-list-row {
  padding: 5px;
  border-top: 0;
}



