@import 'lib/package';

.Badge {
  margin: 0 3px;
  display: inline-block;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  min-width: 89px;
  max-width: 180px;
  text-align: center;
  cursor: default;

  &.slds-badge_lightest {
    height: 25px;
    border: 1px solid #dddbda;
    background-color: $color-gray-1;
    font-size: 0.75rem;
    text-transform: none;
    letter-spacing: normal;

    &:hover {
      background-color: $brand-light-active;
    }
  }

  .Badge__label {
    display: inline-block;
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .Badge__label--with-icon {
    width: calc(100% - 20px);
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .Badge__close {
    float: right;

    &:focus, &:active {
      outline: 0;
      box-shadow: none;
    }

    .Icon {
      position: absolute;
      top: 1px;
      right: -2px;
    }
  }
}

.BadgeList {
  margin-top: 6px;
  display: inline-block;
  position: relative;

  .Badge {
    top: 0;
    display: inline-block;
    position: relative;
    z-index: $z-index-badge;
    margin-left: 0;
    margin-right: 6px;
    margin-bottom: 4px;
  }

  .Badge__stack {
    position: absolute;
    left: 0;
    top: 0;
  }

  &.BadgeList--stacked:hover {
    .Badge {
      background-color: $brand-light-active;
    }
  }
}



