@import 'theme/variables';

.List__item.Modal__selectedrecord {
  padding-bottom: 5px;
  padding-top: 5px;
}

.Modal__selectedrecord-label {
  margin-left: 10px;
  margin-bottom: 5px;
  margin-top: 10px;
}

.Icon.Modal__selectedrecord-error-icon {
  fill: $color-background-error-dark;
  display: inline;
  vertical-align: top;
}

.Button.button-dis-association {
  padding-left: 15px;
}

.Modal__backdrop.SharedEntityModal {
  z-index: zindexprovider('modal') - 1;
}

.Modal.SharedEntityModal {
  z-index: zindexprovider('modal');
}

.Modal__backdrop.EntityReferencePopup {
  z-index: zindexprovider('modal') + 4;
}

.Modal.EntityReferencePopup {
  z-index: zindexprovider('modal') + 5;
}

.SharedEntityModal .ModalContent {
  height: 80%;
}

.SharedEntityModal .ListViewModal {
  height: 99%;
}

.SharedEntityModal .ListViewModal__search + div {
  height: 90%;
}

.SharedEntityModal__container--selected {
  .ListViewModal {
    height: 91%;
  }

  .ListViewModal__search + div {
    height: 90%;
  }
}



